import {
  createRouter,
  createWebHistory
} from 'vue-router'

import Home from '../views/Home.vue'
import User from '../views/User/Index.vue'
import Kantor from '../views/Kantor/Index.vue'
import UnitKerja from '../views/UnitKerja/Index.vue'
import JenisCuti from '../views/JenisCuti/Index.vue'
import TemplateSurat from '../views/TemplateSurat/Index.vue'

import NProgress from 'nprogress'
import 'nprogress/nprogress.css'

const routes = [{
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/user",
    name: "User",
    component: User
  },
  {
    path: "/kantor",
    name: "Kantor",
    component: Kantor
  },
  {
    path: "/unit-kerja", 
    name: "UnitKerja", 
    component: UnitKerja
  },
  {
    path: "/jenis-cuti", 
    name: "JenisCuti", 
    component: JenisCuti
  }, 
  {
    path: "/template-surat", 
    name: "TemplateSurat", 
    component: TemplateSurat
  }
]

const router = createRouter({
  history: createWebHistory(),
  routes // config routes
})

router.beforeResolve((to, from, next) => {
  // If this isn't an initial page load.
  if (to.name) {
    // Start the route progress bar.
    NProgress.start()
  }
  next()
})

router.afterEach((to, from) => {
  // Complete the animation of the route progress bar.
  NProgress.done()
})

export default router