<template>
  <div
    class="pt-10 pb-21"
    style="
      background-image: url('https://presensi.poltekbangplg.ac.id/assets/img/curved-images/curved0.jpg');
      background-size: cover;
    "
  ></div>
  <div class="container-fluid mt-n22">
    <div class="row">
      <div class="col-12">
        <!-- Page header -->
        <div class="d-flex justify-content-between align-items-center">
          <div class="mb-2 mb-lg-0">
            <h3 class="mb-0 fw-bold text-white">Daftar Kantor</h3>
          </div>
        </div>
        <div class="card mt-5">
          <div class="card-body">
            <button
              class="btn btn-primary mb-5"
              data-bs-toggle="modal"
              data-bs-target="#modal"
              style="border-radius: 20px"
            >
              Tambah Data
            </button>
            <div class="table-responsive">
              <table id="myTable" class="table table-striped" width="100%">
                <thead>
                  <tr>
                    <th width="20px;">No</th>
                    <th>Kantor</th>
                    <th>Unit Kerja</th>
                    <th width="10px;"></th>
                    <th width="10px;"></th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>1</td>
                    <td>Politeknik Penerbangan Palembang</td>
                    <td>Bidang Teknologi Informasi</td>
                    <td>
                      <a
                        href="javascript:void(0)"
                        data-bs-toggle="modal"
                        data-bs-target="#modal"
                        class="bi bi-grid text-success"
                        style="font-size: 1.3rem"
                      ></a>
                    </td>
                    <td>
                      <a
                        class="bi bi-trash text-danger"
                        style="font-size: 1.3rem"
                      ></a>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="modal"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">Form Unit Kerja</h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <form id="form">
          <div class="modal-body">
            <input type="hidden" name="id" id="id" />
            <div class="form-group mb-4">
              <label for="">Kantor <sup class="text-danger">*</sup></label>
              <v-select
                :options="kantor"
                placeholder="Kantor"
                required
              ></v-select>
            </div>
            <div class="form-group mb-4">
              <label for="">Unit Kerja <sup class="text-danger">*</sup></label>
              <input
                type="text"
                class="form-control"
                placeholder="Unit Kerja"
                required
              />
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-bs-dismiss="modal"
            >
              Close
            </button>
            <button id="tombol_kirim" class="btn btn-primary">
              <div
                class="spinner-border spinner-border-sm"
                style="
                  margin-right: 3px;
                  width: 0.7rem;
                  height: 0.7rem;
                  display: none;
                "
              ></div>
              Kirim
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  name: "Kantor",

  data() {
    return {
      kantor: [],
      message: "hello world",
    };
  },

  mounted() {
    $("#myTable").DataTable();
    var el = this;
    this.axios
      .get("https://www.themealdb.com/api/json/v1/1/filter.php?c=Vegetarian")
      .then(function (res) {
        let data = res.data.meals;
        data.forEach(function (value, i) {
          el.kantor.push({
            label: value.strMeal,
            code: i.toString(),
          });
        });
      });
  },
};
</script>

<style>
</style>