<template>
  <div
    class="pt-10 pb-21"
    style="
      background-image: url('https://presensi.poltekbangplg.ac.id/assets/img/curved-images/curved0.jpg');
      background-size: cover;
    "
  ></div>
  <div class="container-fluid mt-n22">
    <div class="row">
      <div class="col-12">
        <!-- Page header -->
        <div class="d-flex justify-content-between align-items-center">
          <div class="mb-2 mb-lg-0">
            <h3 class="mb-0 fw-bold text-white">Daftar Kantor</h3>
          </div>
        </div>
        <div class="card mt-5">
          <div class="card-body">
            <button
              class="btn btn-primary mb-5"
              data-bs-toggle="modal"
              data-bs-target="#modal"
              style="border-radius: 20px"
            >
              Tambah Data
            </button>
            <div class="table-responsive">
              <table id="myTable" class="table table-striped" width="100%">
                <thead>
                  <tr>
                    <th>Name</th>
                    <th>Position</th>
                    <th>Office</th>
                    <th>Extn.</th>
                    <th width="10px;"></th>
                    <th width="10px;"></th>
                  </tr>
                </thead>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="modal"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">Form Unit Kerja</h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <form id="form">
          <div class="modal-body">
            <input type="hidden" name="id" id="id" />
            <div class="form-group mb-4">
              <label for="">Unit Kerja <sup class="text-danger">*</sup></label>
              <input
                type="text"
                class="form-control"
                placeholder="Unit Kerja"
                v-model="unit_kerja"
                required
              />
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-bs-dismiss="modal"
            >
              Close
            </button>
            <button id="tombol_kirim" class="btn btn-primary">
              <div
                class="spinner-border spinner-border-sm"
                style="
                  margin-right: 3px;
                  width: 0.7rem;
                  height: 0.7rem;
                  display: none;
                "
              ></div>
              Kirim
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  name: "Kantor",

  data() {
    return {
      kantor: [],
      message: "hello world",
      unit_kerja: "",
      data: [
        {
          id: "1",
          name: "Tiger Nixon",
          job: "System Architect",
          address: "Edinburgh",
          no: "5421",
          tgl: "2011/04/25",
          salary: "$320,800",
        },
        {
          id: "2",
          name: "Garrett Winters",
          job: "Accountant",
          address: "Tokyo",
          no: "8422",
          tgl: "2011/07/25",
          salary: "$170,750",
        },
      ],
    };
  },

  mounted() {
    let rel = this;
    var table = $("#myTable").DataTable({
      data: this.data,
      columns: [
        { data: "name" },
        { data: "job" },
        { data: "address" },
        { data: "no" },
        {
          name: "edit",
          render: function (data, type, row, meta) {
            return `<a
              href="javascript:void(0)"
              data-bs-toggle="modal"
              data-bs-target="#modal"
              id="editButton"
              class="bi bi-grid text-success"
              style="font-size: 1.3rem"
            ></a>`;
          },
        },
        {
          name: "hapus",
          render: function () {
            return `<a
              id="deleteButton"
              href="javascript:void(0)"
              class="bi bi-trash text-danger"
              style="font-size: 1.3rem"
            ></a>`;
          },
        },
      ],
    });

    //datatable click handle hapus
    $("#myTable tbody").on("click", "#deleteButton", function () {
      var data = table.row($(this).parents("tr")).data();
      rel.deleteData(data.id, data.name);
    });

    //datatable click handle edit
    var exampleModal = document.getElementById("modal");
    exampleModal.addEventListener("show.bs.modal", function (event) {

      var dataEdit;

      $("#myTable tbody").on("click", "#editButton", function () {
        dataEdit = table.row($(this).parents("tr")).data();
        if (dataEdit) {
          console.log(dataEdit.name);
          rel.unit_kerja = dataEdit.name;
        }
      });

      rel.unit_kerja = ''
    });
  },

  methods: {
    //menghapus data
    deleteData(id, name) {
      this.$swal
        .fire({
          title: "Yakin hapus data " + name + " dengan id " + id + "?",
          icon: "warning",
          showCancelButton: true,
          confirmButtonColor: "#d33",
          confirmButtonText: "Ya",
          cancelButtonColor: "#3085d6",
          cancelButtonText: "Batal",
        })
        .then((result) => {
          if (result.value) {
            console.log("success");
          }
        });
    },
  },
};
</script>

