<template>
  <div
    class="pt-10 pb-21"
    style="
      background-image: url('https://presensi.poltekbangplg.ac.id/assets/img/curved-images/curved0.jpg');
      background-size: cover;
    "
  ></div>
  <div class="container-fluid mt-n22">
    <div class="row">
      <div class="col-12">
        <!-- Page header -->
        <div class="d-flex justify-content-between align-items-center">
          <div class="mb-2 mb-lg-0">
            <h3 class="mb-0 fw-bold text-white">Daftar User</h3>
          </div>
        </div>
        <div class="card mt-5">
          <div class="card-body">
            <button
              class="btn btn-primary mb-5"
              data-bs-toggle="modal"
              data-bs-target="#modal"
              style="border-radius: 20px"
            >
              Tambah Data
            </button>
            <div class="table-responsive">
              <table id="myTable" class="table table-striped" width="100%">
                <thead>
                  <tr>
                    <th>No</th>
                    <th>Foto</th>
                    <th>Name</th>
                    <th>Email</th>
                    <th>Role</th>
                    <th width="10px"></th>
                    <th width="10px"></th>
                  </tr>
                </thead>
                <tbody></tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="modal"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">Form User</h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <form id="form">
          <div class="modal-body">
            <input type="hidden" name="id" id="id" />
            <div class="form-group mb-4">
              <label for="">Nama <sup class="text-danger">*</sup></label>
              <input
                type="text"
                class="form-control"
                id="nama"
                placeholder="Nama"
                required
              />
            </div>
            <div class="form-group mb-4">
              <label for="">Email <sup class="text-danger">*</sup></label>
              <input
                type="email"
                class="form-control"
                placeholder="Email"
                id="email"
                required
              />
            </div>
            <div class="form-group mb-4">
              <label for="">Password <sup class="text-danger">*</sup></label>
              <input
                type="password"
                class="form-control"
                placeholder="Password"
                id="password"
                required
              />
            </div>
            <div class="form-group mb-4">
              <label for="">Role <sup class="text-danger">*</sup></label>
              <select id="role" class="form-select">
                <option value="0">Admin</option>
              </select>
            </div>
            <div class="form-group mb-4">
              <label for="">Foto <sup class="text-danger">*</sup></label>
              <input type="file" class="form-control" id="foto" required />
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-bs-dismiss="modal"
            >
              Close
            </button>
            <button id="tombol_kirim" class="btn btn-primary">
              <div
                class="spinner-border spinner-border-sm"
                style="
                  margin-right: 3px;
                  width: 0.7rem;
                  height: 0.7rem;
                  display: none;
                "
              ></div>
              Kirim
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import $ from "jquery";
import avatar from '../../assets/logo.png'

export default {
  name: "User",

  data(){
    return{
      data  : '', 
    }
  },

  mounted() {
    $("#myTable").DataTable({
      processing: true,
      serverSide: true,
      // ordering: false,
      bDestroy: true,
      ajax: {
        url: process.env.VUE_APP_ROOT_URL+"/api/user",
      },
      columns: [
        {
          data: "no",
          name: "id",
          render: function (data, type, row, meta) {
            return meta.row + meta.settings._iDisplayStart + 1;
          },
        },
        {
          data: "foto_profil",
          name: "foto_profil",
          render: function(data, type, row, meta){
            if(row.foto_profil != null){
              return 'ada'
            }else{
              return `<img width="50px" src="`+avatar+`">`
            }
          }
        },
        {
          data: "name",
          name: "name",
        },
        {
          data: "email",
          name: "email",
        },
        {
          data: "role",
          name: "role",
          render: function(data, type, row, meta){
            if(row.role == '0'){
              return `<div class="badge bg-success">Super Admin</div>`
            }else if(row.role == '1'){
              return `<div class="badge bg-info">Admin Pegawai</div>`
            }
          }
        },
       {
          name: "edit",
          render: function () {
            return `<a
              id="deleteButton"
              href="javascript:void(0)"
              class="bi bi-grid text-success"
              style="font-size: 1.3rem"
            ></a>`;
          },
       },
       {
          name: "hapus",
          render: function () {
            return `<a
              id="deleteButton"
              href="javascript:void(0)"
              class="bi bi-trash text-danger"
              style="font-size: 1.3rem"
            ></a>`;
          },
       },
      ],
    });
  },
};
</script>

<style>
</style>