<template>
  <div
    class="pt-10 pb-21"
    style="
      background-image: url('https://presensi.poltekbangplg.ac.id/assets/img/curved-images/curved0.jpg');
      background-size: cover;
    "
  ></div>
  <div class="container-fluid mt-n22">
    <div class="row">
      <div class="col-12">
        <!-- Page header -->
        <div class="d-flex justify-content-between align-items-center">
          <div class="mb-2 mb-lg-0">
            <h3 class="mb-0 fw-bold text-white">Daftar Jenis Cuti</h3>
          </div>
        </div>
        <div class="card mt-5">
          <div class="card-body">
            <button
              class="btn btn-primary mb-5"
              data-bs-toggle="modal"
              data-bs-target="#modal"
              style="border-radius: 20px"
            >
              Tambah Data
            </button>
            <div class="table-responsive">
              <table id="myTable" class="table table-striped" width="100%">
                <thead>
                  <tr>
                    <th width="20px;">No</th>
                    <th>Jenis Cuti</th>
                    <th>Jumlah Cuti</th>
                    <th>Keterangan</th>
                    <th>Tahun</th>
                    <th width="10px;"></th>
                    <th width="10px;"></th>
                  </tr>
                </thead>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div
    class="modal fade"
    id="modal"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">Form Jenis Cuti</h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <form id="form" @submit.prevent="addOrUpdate">
          <div class="modal-body">
            <input type="hidden" name="id" id="id" />
            <div class="form-group mb-4">
              <label for="">Jenis Cuti <sup class="text-danger">*</sup></label>
              <input
                type="text"
                class="form-control"
                placeholder="Jenis Cuti"
                required
              />
            </div>
            <div class="form-group mb-4">
              <label for="">Jumlah Cuti <sup class="text-danger">*</sup></label>
              <input
                type="text"
                class="form-control"
                placeholder="Jumlah Cuti Pertahun"
                required
              />
            </div>
            <div class="form-group mb-4">
              <label for="">Tahun <sup class="text-danger">*</sup></label>
              <input
                type="number"
                class="form-control"
                placeholder="tahun kerja"
                required
              />
            </div>
            <div class="form-group mb-4">
              <label for="">Keterangan <sup class="text-danger">*</sup></label>
              <textarea
                cols="30"
                rows="5"
                class="form-control"
                placeholder="keterangan cuti"
                required
              ></textarea>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-bs-dismiss="modal"
            >
              Close
            </button>
            <button id="tombol_kirim" class="btn btn-primary">
              <div
                class="spinner-border spinner-border-sm"
                style="
                  margin-right: 3px;
                  width: 0.7rem;
                  height: 0.7rem;
                  display: none;
                "
              ></div>
              Kirim
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import $ from "jquery";

export default {
  name: "Kantor",

  data() {
    return {
      cuti: [
        {
          No: "1",
          jenis_cuti: "Cuti Melahirkan",
          jumlah_cuti: "40 Hari",
          keterangan_cuti: "Cuti ibu hamil",
          tahun: "2020",
        },
      ],
      message: "hello world",
    };
  },

  mounted() {
    $("#myTable").DataTable({
      data: this.cuti,
      columns: [
        { data: "No" },
        { data: "jenis_cuti" },
        { data: "jumlah_cuti" },
        { data: "keterangan_cuti" },
        { data: "tahun" },
        {
          name: "edit",
          render: function (data) {
            return `<a
              href="javascript:void(0)"
              data-bs-toggle="modal"
              data-bs-target="#modal"
              class="bi bi-grid text-success"
              style="font-size: 1.3rem"
            ></a>`;
          },
        },
        {
          name: "hapus",
          render: function (data) {
            return `<a
              href="javascript:void(0)"
              @click="deleteData('1', 'tes')"
              class="bi bi-trash text-danger"
              style="font-size: 1.3rem"
            ></a>`;
          },
        },
      ],
    });
  },

  methods: {
    deleteData(id, name) {
      this.$swal
        .fire({
          title: "Yakin hapus data " + name + " dengan id " + id + "?",
          icon: "warning",
          showCancelButton: true,
          confirmButtonColor: "#d33",
          confirmButtonText: "Ya",
          cancelButtonColor: "#3085d6",
          cancelButtonText: "Batal",
        })
        .then((result) => {
          if (result.value) {
            console.log("success");
          }
        });
    },

    addOrUpdate() {
      let rel = this;
      axios
        .get("https://www.themealdb.com/api/json/v1/1/filter.php?c=Vegetarian")
        .then(function (res) {
          rel.$swal.fire({
            icon: "success",
            title: "Sukses",
            text: "res.data.respon",
            timer: 3000,
            showConfirmButton: false,
          });
        });
    },
  },
};
</script>

<style>
</style>